.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 10;
  background: rgba(0, 0, 0, 0.75);
}

.modal {
  position: fixed;
  top: 30vh;
  /*   left: 10%;
  width: 80%; */
  z-index: 100;
  overflow: hidden;
  background-color: white;
}

.header {
  background: #4f005f;
  padding: 1rem;
}

.header h2 {
  margin: 0;
  color: white;
  text-align: center;
}

.content {
  /*   padding: 1rem; */
  text-align: left;
  font-size: large;
  font-weight: bold;
}

.content p {
  color: black;
}

.actions {
  padding: 1rem;
  display: flex;
  justify-content: flex-end;
}

@media (min-width: 768px) {
  .modal {
    left: calc(50% - 25rem);
    width: 50rem;
  }
}
