.button {
  font: inherit;
  cursor: pointer;
  padding: 1rem 2rem;
  border: 1px solid #40005d;
  background-color: #40005d;
  color: white;
  border-radius: 12px;
  margin-right: 1rem;
}

.button:hover,
.button:active {
  background: #741188;
  border-color: #741188;
}

.button.alternative {
  color: #220131;
  border-color: transparent;
  background-color: transparent;
}

.button:focus {
  outline: none;
}

.button.alternative:hover,
.button.alternative:active {
  background-color: #ddb3f8;
}
