.new-task__controls {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  margin-bottom: 1rem;
  text-align: left;
  margin-top: 10px;
  margin-left: 10px;
}

.new-task__control label {
  font-weight: bold;
  margin-bottom: 0.5rem;
  display: block;
}

.new-task__control input {
  font: inherit;
  padding: 0.5rem;
  border-radius: 6px;
  border: 1px solid #ccc;
  width: 20rem; /* 37 */
  max-width: 100%;
}

.new-task__actions {
  text-align: right;
}

.RangeRed {
  accent-color: red;
}
.RangeGreen {
  accent-color: green;
}
