.task-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
  margin: 1rem 0;
  background-color: #4b4b4b;
}
.task-item-check {
  background-color: rgb(126, 238, 126);
}
.task-item__description {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  align-items: flex-end;
  flex-flow: column-reverse;
  justify-content: flex-start;
  flex: 1;
}
.task-item-without-check {
  font-size: 1rem;
  flex: 1;
  margin: 0 1rem;
  color: white;
}

.task-item-check-true {
  text-decoration-line: line-through;
  text-decoration-color: red;
  color: black;
}

.task-item__range {
  font-size: 1rem;
  font-weight: bold;
  color: white;
  background-color: #40005d;
  border: 1px solid white;
  padding: 0.5rem;
  border-radius: 12px;
}

@media (min-width: 580px) {
  .task-item__description {
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    flex: 1;
  }

  .task-item__description h2 {
    font-size: 1.25rem;
  }

  .task-item__range {
    font-size: 1.25rem;
    padding: 0.5rem 1.5rem;
  }
  .time {
    color: red;
  }
}

.button-task-container button {
  margin: 0 5px;
  padding: 5px 10px;
  font-size: 10px;
  border-radius: 12px;
  background-color: transparent;
  background-color: aliceblue;
  font-size: 15px;
}

.edit {
  border: 1.5px solid rgb(141, 181, 145);
  color: rgb(141, 181, 145);
}

.edit:hover {
  background-color: rgb(182, 223, 186);
}

.edit:active {
  color: white;
  background-color: rgb(141, 181, 145);
}

.delete {
  border: 1.5px solid rgb(255, 175, 163);
  color: rgb(255, 175, 163);
}

.delete:hover {
  background-color: rgb(255, 201, 193);
}

.delete:active {
  color: white;
  background-color: rgb(255, 175, 163);
}

.fileopen {
  width: 50px;
  height: 50px;
  color: rgb(176, 12, 201);
  border-radius: 12px;
}

.fileopen:hover {
  color: white;
  background-color: rgb(227, 113, 243);
}

.fileopen:active {
  background-color: rgb(227, 113, 243);
}

.icons {
  margin: -13px 12px;
  color: red;
  width: 50px;
  height: 36px;
}
